@import '../variables';

.wrapper {
  max-width: 1270px + $contMargin;
  margin: 0 auto;
}

.container {
  position: relative;
  margin: 0 $contMargin;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  overflow: hidden;

  .content {
    margin: 0 auto;
    width: 635px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background-color: #e2e2e2;
    z-index: 1;

    .main {
      font-family: 'Roboto Slab', Tahoma, serif;
      font-size: clamp(0.675rem, calc(0.2rem + 0.85vw), 0.875rem);
      width: 100%;
      padding: 0 30px;

      @media (max-width: 620px) {
        padding: 0 5px;
      }

      .header {
        font-family: Montserrat, Roboto, system-ui, Tahoma, sans-serif;
        font-size: clamp(0.8rem, calc(0.2rem + 1vw), 1rem);
        padding: 5px 0;
        text-align: center;
      }

      ol {
        padding: 10px 0;
        padding-left: 20px;
        list-style-type: circle;
      }

      .phone {
        display: inline-block;
        background-color: inherit;
        color: inherit;
        text-decoration: none;
        margin-top: 5px;
      }

      .address {
        text-align: center;
      }
    }
  }
}
